import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.router.dom.browserRouter
import react.router.dom.route
import react.router.dom.switch
import view.components.pokemonDetailView
import view.detail.PokemonDetailProps
import view.list.PokemonListView

interface IdProps : RProps {
    var id: Int
}

class App : RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        browserRouter {
            switch {
                route("/", PokemonListView::class, exact = true)
                route("/:page:name", PokemonListView::class, exact = true)
                route<IdProps>("/pokemon/:id") { props ->
                    pokemonDetailView { optionalProps, children ->
                        optionalProps {
                            //...Set props here
                        }
                        children {
                            //...Add children here
                        }
                        PokemonDetailProps(props.match.params.id)
                    }
                }
            }
        }
    }
}