package view.detail.components

import react.RBuilder
import util.addText
import util.setProps
import view.components.chakra.Box

fun RBuilder.BlockHeader(bgColor: String, text: String? = null) {
    Box {
        setProps {
            mt = "32px"
            mx = "40px"
            py = "4px"
            px = "16px"
            backgroundColor = bgColor
            color = "white"
            fontSize = "16px"
            fontWeight = "bold"
        }
        text?.let { addText(it) }
    }
}