package view.list.components

import data.model.Pokemon
import react.RElementBuilder
import react.RProps
import react.dom.hr
import util.addText
import util.setOnClickListener
import util.setOnHover
import util.setProps
import view.components.TypeList
import view.components.chakra.Flex
import view.components.chakra.Image
import view.components.chakra.PseudoBox
import view.components.chakra.Text

fun RElementBuilder<RProps>.PokemonCard(pokemon: Pokemon, onPokemonClicked: (Pokemon) -> Unit) {
    PseudoBox {
        setProps {
            w = "100%"
            py = "16px"
            rounded = "sm"
            shadow = "sm"
            bg = "white"
            cursor = "pointer"
        }

        setOnHover {
            shadow = "md"
            position = "relative"
            top = "-2px"
        }

        setOnClickListener {
            onPokemonClicked(pokemon)
        }

        Text {
            setProps {
                mb = "16px"
                mx = "24px"
                fontSize = "16px"
                fontWeight = "bold"
            }
            addText(pokemon.name)
        }

        hr {}

        Flex {
            setProps {
                justify = "center"
            }
            Image {
                setProps {
                    mt = "16px"
                    size = "104px"
                    src = pokemon.image
                }
            }
        }

        TypeList(pokemon.types) {
            setProps {
                mt = "16px"
                mx = "24px"
            }
        }
    }
}