package view.detail

import data.model.PokemonDetailResponse
import kotlinext.js.jsObject
import util.AxiosConfigSettings
import util.axios

class PokemonDetailPresenter(private val view: IPokemonDetailView) {

    var pokemonId: String? = null
        private set(value) {
            field = value
        }

    fun start(id: Int) {
        this.pokemonId = getFullPokemonId(id.toString())
        loadStorage(id)
    }

    fun onBackPressed() {
        view.goBack()
    }

    private fun getFullPokemonId(id: String): String {
        var result = id
        while (result.length < 3) {
            result = "0$result"
        }
        return result
    }

    private fun loadStorage(id: Int) {
        getPokemonDetail(id)
        //TODO handle state refresh
    }

    private fun getPokemonDetail(pokemonId: Int) {
        view.showProgress()
        val config: AxiosConfigSettings = jsObject {
            this.url = "https://intern-pokedex.myriadapps.com/api/v1/pokemon/$pokemonId"
            timeout = 3000
        }
        axios<PokemonDetailResponse>(config).then { response ->
            view.hideProgress()
            view.onPokemonReceived(response.data.data)
        }.catch { error ->
            view.hideProgress()
            view.showError(error.message ?: "An unexpected error occurred")
        }
    }

    private fun save(data: Any) {
        //TODO handle state saving
    }
}