package view.list

import data.model.LinkSet
import data.model.PokemonListResponse
import kotlinext.js.jsObject
import kotlinx.coroutines.Job
import kotlinx.coroutines.delay
import kotlinx.coroutines.launch
import mvp.BasePresenter
import util.AxiosConfigSettings
import util.axios
import kotlin.browser.localStorage

class PokemonListPresenter(private val view: IPokemonListView) : BasePresenter() {
    private lateinit var linkSet: LinkSet
    private var search: String = ""
    private var currentPage: String = "https://intern-pokedex.myriadapps.com/api/v1/pokemon?page=1"
    private var job: Job? = null

    fun start() {
        loadStorage()
    }

    fun getPreviousPage() {
        currentPage = linkSet.prev!!
        view.showProgress()
        getPokemonList()
    }

    fun getNextPage() {
        currentPage = linkSet.next!!
        view.showProgress()
        getPokemonList()
    }

    fun search(search: String) {
        this.search = search
        view.showProgress()
        view.setSearch(search)
        currentPage = "https://intern-pokedex.myriadapps.com/api/v1/pokemon?page=1"
        job?.cancel()
        job = presenterScope.launch {
            delay(300L)
            getPokemonList()
        }
    }

    private fun loadStorage() {
        search = localStorage.getItem("search") ?: ""
        view.setSearch(search)
        localStorage.getItem("data")?.let {
            val data = JSON.parse<PokemonListResponse>(it)
            linkSet = data.links
            view.onPokemonReceived(
                pokemonList = data.data,
                hasPrevious = data.links.prev != null,
                hasNext = data.links.next != null
            )
        } ?: getPokemonList()
    }

    private fun getPokemonList() {
        val config: AxiosConfigSettings = jsObject {
            this.url = "$currentPage&name=$search"
            timeout = 3000
        }
        axios<PokemonListResponse>(config).then { response ->
            linkSet = response.data.links
            view.hideProgress()
            save(response.data)
            view.onPokemonReceived(
                pokemonList = response.data.data,
                hasPrevious = response.data.links.prev != null,
                hasNext = response.data.links.next != null
            )
        }.catch { error ->
            view.hideProgress()
            view.showError(error.message ?: "An unexpected error occurred")
        }
    }

    private fun save(data: Any) {
        localStorage.setItem("data", JSON.stringify(data))
        localStorage.setItem("search", search)
    }
}