package view.list

import data.model.Pokemon
import kotlinext.js.jsObject
import mvp.BaseState
import mvp.BaseView
import org.w3c.dom.HTMLInputElement
import react.RBuilder
import react.RProps
import react.router.dom.redirect
import react.setState
import util.*
import view.components.ProgressIndicator
import view.components.chakra.*
import view.list.components.PokemonCard
import kotlin.browser.document
import kotlin.browser.localStorage

class PokemonListView : BaseView<RProps, PokemonListState>(), IPokemonListView {

    private val presenter = PokemonListPresenter(this)

    override fun PokemonListState.init() {
        pokemonList = mutableListOf()
        hasPreviousPage = false
        hasNextPage = false
        search = ""
        selectedPokemon = null
    }

    override fun componentDidMount() {
        presenter.start()
        document.bgColor = "#55a69c"
    }

    override fun RBuilder.render() {
        state.selectedPokemon?.let {
            val blendedColor = modifyColor(getBlendedColor(it.types.first(), it.types.last()), -15)
            localStorage.setItem("blendedColor", blendedColor)
            redirect("/", "/pokemon/${it.id}", true)
            return
        }
        Flex {
            setProps {
                justifyContent = "space-between"
                alignItems = "center"
                w = "100%"
            }
            IconButton {
                setProps {
                    m = "56px"
                    h = "64px"
                    w = "64px"
                    visibility = if (state.hasPreviousPage) "visible" else "hidden"
                    isRound = true
                    variantColor = "green"
                    icon = "arrow-back"
                }
                setOnClickListener {
                    presenter.getPreviousPage()
                }
            }
            InputGroup {
                Input {
                    setProps {
                        height = "96px"
                        width = "456px"
                        fontSize = "72px"
                        fontWeight = "bold"
                        placeholder = "Search..."
                        value = state.search
                        pr = "96px"
                        style = jsObject {
                            background = "rgba(0,0,0,0.04)"
                            transition = ".3s"
                        }
                        variant = "filled"
                        focusBorderColor = "null"
                        color = "white"
                    }

                    setOnFocus {
                        borderColor = "null"
                        shadow = "md"
                        position = "relative"
                        top = "-2px"
                        width = "600px"
                    }

                    //Prevents default hover behaviour
                    setOnHover {}

                    setOnChangeListener {
                        presenter.search((it.target as HTMLInputElement).value)
                    }
                }

                if (state.loading) {
                    InputRightElement {
                        setProps {
                            height = "100%"
                            align = "center"
                            mx = "32px"
                        }
                        ProgressIndicator {
                            setProps {
                                size = "32px"
                                cursor = "none"
                            }
                        }
                    }
                } else {
                    if (state.search.isNotEmpty()) {
                        InputRightElement {
                            setProps {
                                height = "100%"
                                align = "center"
                                mx = "32px"
                            }
                            Icon {
                                setProps {
                                    background = null
                                    name = "close"
                                    size = "32px"
                                    cursor = "pointer"
                                }
                                setOnClickListener {
                                    presenter.search("")
                                }
                                setOnMouseDown {
                                    it.preventDefault()
                                }
                            }
                        }
                    }
                }
            }
            IconButton {
                setProps {
                    m = "56px"
                    h = "64px"
                    w = "64px"
                    visibility = if (state.hasNextPage) "visible" else "hidden"
                    isRound = true
                    variantColor = "green"
                    icon = "arrow-forward"
                }

                setOnClickListener {
                    presenter.getNextPage()
                }
            }
        }
        Grid {
            setProps {
                mx = "48px"
                mb = "64px"
                justifyContent = "center"
                templateColumns = "repeat( auto-fit, minmax(220px, 220px) )"
                gap = "16px"
            }
            state.pokemonList.forEach { pokemon ->
                PokemonCard(pokemon, ::onPokemonClicked)
            }
        }
    }

    override fun onPokemonReceived(
        pokemonList: Array<Pokemon>,
        hasPrevious: Boolean,
        hasNext: Boolean
    ) {
        setState {
            this.pokemonList = pokemonList.toList()
            this.hasPreviousPage = hasPrevious
            this.hasNextPage = hasNext
        }
    }

    override fun setSearch(searchValue: String) {
        setState {
            search = searchValue
        }
    }

    private fun onPokemonClicked(pokemon: Pokemon) {
        setState { selectedPokemon = pokemon }
    }
}

interface PokemonListState : BaseState {
    var pokemonList: List<Pokemon>
    var hasPreviousPage: Boolean
    var hasNextPage: Boolean
    var search: String
    var selectedPokemon: Pokemon?
}