package view.detail

import data.model.PokemonDetail
import mvp.BaseState
import mvp.BaseView
import org.w3c.dom.get
import react.RBuilder
import react.RProps
import react.router.dom.redirect
import react.setState
import util.addText
import util.setOnClickListener
import util.setProps
import view.components.chakra.Box
import view.components.chakra.Flex
import view.components.chakra.IconButton
import view.components.chakra.Text
import view.detail.components.DetailPanel
import kotlin.browser.document
import kotlin.browser.localStorage

class PokemonDetailView : BaseView<PokemonDetailProps, PokemonDetailState>(), IPokemonDetailView {

    private val presenter = PokemonDetailPresenter(this)

    override fun PokemonDetailState.init() {
        pokemon = null
        goBack = false
    }

    override fun componentDidMount() {
        state.bgColor = localStorage["blendedColor"] ?: "#ffffff"
        localStorage.removeItem("blendedColor")
        document.bgColor = state.bgColor
        presenter.start(props.pokemonId)
    }

    override fun RBuilder.render() {
        if (state.goBack) {
            redirect("/pokemon/${props.pokemonId}", "/", push = true, exact = false)
        } else {
            if (state.loading) {
                //TODO add some loading indicator
            }
            state.pokemon?.let { pokemon ->
                Flex {
                    setProps {
                        justifyContent = "space-between"
                        alignItems = "center"
                        w = "100%"
                    }
                    IconButton {
                        setProps {
                            m = "56px"
                            h = "64px"
                            w = "64px"
                            isRound = true
                            background = "#ffffff"
                            color = state.bgColor
                            icon = "arrow-back"
                        }
                        setOnClickListener {
                            presenter.onBackPressed()
                        }
                    }
                    Text {
                        setProps {
                            color = "white"
                            fontWeight = "bold"
                            fontSize = "72px"
                        }
                        addText(pokemon.name)
                    }
                    //spacer
                    Box {
                        setProps {
                            m = "56px"
                            h = "64px"
                            w = "64px"
                        }
                    }
                }

                DetailPanel(pokemon, presenter.pokemonId, state.bgColor)
            }
        }
    }

    override fun onPokemonReceived(pokemon: PokemonDetail) {
        setState {
            this.pokemon = pokemon
        }
    }

    override fun goBack() {
        setState {
            goBack = true
        }
    }
}

data class PokemonDetailProps(
    var pokemonId: Int
) : RProps

interface PokemonDetailState : BaseState {
    var pokemon: PokemonDetail?
    var goBack: Boolean
    var bgColor: String
}