package view.components

import kotlinx.css.properties.*
import react.RBuilder
import react.RElementBuilder
import react.RProps
import styled.*
import util.setProps
import view.components.chakra.Icon

fun RBuilder.ProgressIndicator(block: (RElementBuilder<RProps>.() -> Unit)? = null) {

    (styled(Icon)) {
        setProps {
            name = "spinner"
        }

        css {
            animation(
                duration = Time("750ms"),
                timing = Timing.linear,
                delay = Time("0s"),
                iterationCount = IterationCount("infinite"),
                direction = AnimationDirection.normal,
                fillMode = FillMode.none,
                playState = PlayState.running
            ) {
                from {
                    transform {
                        rotate(Angle("0deg"))
                    }
                }

                to {
                    transform {
                        rotate(Angle("359deg"))
                    }
                }
            }
        }

        block?.invoke(this)
    }
}