package view.detail.components

import data.model.Attribute
import react.RBuilder
import util.addText
import util.setProps
import view.components.chakra.Stack
import view.components.chakra.Text

fun RBuilder.AttributeView(attribute: Attribute) {
    Stack {
        setProps {
            isInline = true
        }
        Text {
            setProps {
                fontSize = "16px"
                fontWeight = "bold"
                w = "100px"
            }
            addText("${attribute.name}:")
        }
        Stack {
            attribute.values.forEachIndexed { idx, it ->
                Text {
                    setProps {
                        fontSize = "16px"
                    }
                    addText(if (idx < attribute.values.size - 1) "$it," else it)
                }
            }
        }
    }
}