package util

import kotlin.math.round

fun getTypeBgColor(type: String): String {
    //source: http://www.epidemicjohto.com/t882-type-colors-hex-colors
    return when (type.toLowerCase()) {
        "normal" -> "#A8A77A"
        "fire" -> "#EE8130"
        "water" -> "#6390F0"
        "electric" -> "#F7D02C"
        "grass" -> "#7AC74C"
        "ice" -> "#96D9D6"
        "fighting" -> "#C22E28"
        "poison" -> "#A33EA1"
        "ground" -> "#E2BF65"
        "flying" -> "#A98FF3"
        "psychic" -> "#F95587"
        "bug" -> "#A6B91A"
        "rock" -> "#B6A136"
        "ghost" -> "#735797"
        "dragon" -> "#6F35FC"
        "dark" -> "#705746"
        "steel" -> "#B7B7CE"
        "fairy" -> "#D685AD"
        else -> "#000000"
    }
}

fun modifyColor(color: String, percent: Int): String {
    //(converted to Kotlin)
    //source: https://gist.github.com/renancouto/4675192
    val num = color.replace("#", "").toInt(16)
    val amt = round(2.55 * percent).toInt()
    val r = (num shr 16) - amt
    val b = (num shr 8 and 0x00FF) - amt
    val g = (num and 0x0000FF) - amt

    return "#" + (0x1000000 + getRGBValue(r) * 0x10000 + getRGBValue(b) * 0x100 + getRGBValue(g)).toString(16).drop(1)
}

fun getRGBValue(value: Int): Int {
    var result = 0
    if (value >= 255) {
        result = 255
    } else if (value >= 1) {
        result = value
    }
    return result
}

fun rgbToHex(rgb: Int): String {
    var hex = rgb.toString(16);
    if (hex.length < 2) {
        hex = "0$hex";
    }
    return hex;
}

fun fullColorHex(r: Int, g: Int, b: Int): String {
    val red = rgbToHex(r)
    val green = rgbToHex(g)
    val blue = rgbToHex(b)
    return "#$red$green$blue"
}

fun getBlendedColor(color1: String, color2: String?): String {
    //(converted to Kotlin)
    //source: https://www.programming-idioms.org/idiom/154/halfway-between-two-hex-color-codes
    var c = "#";
    val c1 = getTypeBgColor(color1);
    val c2 = if (color2 != null) getTypeBgColor(color2) else c1
    for (i in 0 until 3) {
        val sub1 = c1.substring(1 + 2 * i, 3 + 2 * i);
        val sub2 = c2.substring(1 + 2 * i, 3 + 2 * i);
        val v1 = sub1.toInt(16);
        val v2 = sub2.toInt(16);
        val v = (v1 + v2) / 2
        val sub = v.toString(16).toUpperCase();
        val padsub = ("0$sub").drop(1)
        c += padsub;
    }
    return c
}

