package view.detail.components

import data.model.Stats
import react.RBuilder
import util.addText
import util.modifyColor
import util.setProps
import view.components.chakra.Box
import view.components.chakra.Stack
import view.components.chakra.Text

fun RBuilder.StatView(stats: Stats, bgColor: String) {
    val statObjs = listOf(
        StatPair("HP", stats.hp),
        StatPair("Attack", stats.attack),
        StatPair("Defense", stats.defense),
        StatPair("Speed", stats.speed)
//        StatPair("Special", stats.special ?: 0)
    )

    Stack {
        setProps {
            spacing = "4px"
            w = "100%"
            ml = "95px"
        }
        statObjs.forEach {
            Stack {
                setProps {
                    isInline = true
                }
                Text {
                    setProps {
                        w = "100px"
                    }
                    addText(it.name)
                }
                Box {
                    setProps {
                        minW = "100px"
                        w = "100%"
                        bg = modifyColor(bgColor, -20)
                    }
                    Box {
                        setProps {
                            backgroundColor = bgColor
                            color = "white"
                            pl = "4px"
                            w = "${(it.value / 255.0) * 100}%"
                        }
                        addText("${it.value}")
                    }
                }
            }
        }
    }
}

private data class StatPair(val name: String, val value: Int)