package view.detail.components

import data.model.Attribute
import data.model.PokemonDetail
import react.RBuilder
import react.dom.hr
import util.addText
import util.setProps
import view.components.TypeList
import view.components.chakra.*

fun RBuilder.DetailPanel(pokemon: PokemonDetail, pokemonId: String?, bgColor: String) {
    Box {
        setProps {
            py = "16px"
            rounded = "sm"
            shadow = "sm"
            background = "#ffffff"
            minW = "600px"
            mx = "180px"
        }
        Stack {
            setProps {
                isInline = true
                mb = "16px"
                px = "24px"
            }
            Text {
                setProps {
                    fontSize = "16px"
                    fontWeight = "bold"
                }
                addText(pokemon.name)
            }
            Text {
                setProps {
                    fontSize = "16px"
                    fontWeight = "bold"
                    color = "#bababa"
                }
                addText("#$pokemonId")
            }
            TypeList(pokemon.types) {
                setProps {
                    width = "100%"
                }
            }
        }

        hr {}

        Stack {
            setProps {
                isInline = true
                mt = "48px"
                mx = "72px"
                alignItems = "center"
            }
            Image {
                setProps {
                    size = "130px"
                    src = "https://assets.pokemon.com/assets/cms2/img/pokedex/full/${pokemonId}.png"
                }
            }
            StatView(pokemon.stats, bgColor)
        }

        Text {
            setProps {
                mx = "56px"
                mt = "48px"
                fontSize = "16px"
                fontWeight = "bold"
            }
            addText(pokemon.genus)
        }

        Text {
            setProps {
                mx = "56px"
                mt = "8px"
                fontSize = "16px"
            }
            addText(pokemon.description)
        }

        BlockHeader(bgColor, "Profile")
        Grid {
            setProps {
                mx = "56px"
                mt = "24px"
                templateColumns = "repeat( auto-fit, minmax(200px, 200px) )"
                gap = "16px"
            }
            getAttributes(pokemon).forEach {
                AttributeView(it)
            }
        }
        //BlockHeader(bgColor, "Damage When Attacked")
    }
}

private fun getAttributes(pokemon: PokemonDetail): List<Attribute> {
    return listOf(
        Attribute("Height", "${pokemon.height} m"),
        Attribute("Weight", "${pokemon.weight} kg"),
        Attribute("Egg Groups", *pokemon.eggGroups ?: arrayOf("none")),
        Attribute("Abilities", *pokemon.abilities)
    )
}