package view.components

import react.RBuilder
import react.RElementBuilder
import react.RProps
import util.addText
import util.getTypeBgColor
import util.modifyColor
import util.setProps
import view.components.chakra.Box
import view.components.chakra.Stack

fun RBuilder.TypeList(types: Array<String>, block: (RElementBuilder<RProps>.() -> Unit)? = null) {
    Stack {
        setProps {
            justify = "flex-end"
            isInline = true
            spacing = "8px"
        }
        types.sorted().forEach {
            Box {
                setProps {
                    px = "4px"
                    border = "1px"
                    borderColor = modifyColor(getTypeBgColor(it), 15)
                    background = modifyColor(getTypeBgColor(it), -45)
                    rounded = "md"
                    color = modifyColor(getTypeBgColor(it), 15)
                }
                addText(it.toUpperCase())
            }
        }
        block?.invoke(this)
    }
}